body {
  font-family: 'Paperlogy-4Regular';
  font-size: 14px;
}

/* ================= LoACoreOptimizer.css 추가 ================ */
@keyframes loaPulse {
  0%   { box-shadow: 0 0 0 0 rgba(163,153,242,0.55); }
  50%  { box-shadow: 0 0 0 6px rgba(163,153,242,0.25); }
  100% { box-shadow: 0 0 0 0 rgba(163,153,242,0.0); }
}
.LoA-highlight{
  position: relative;
  outline: 2px solid #a399f2;
  border-color: #a399f2 !important;
  animation: loaPulse .9s ease-in-out 3;
  transition: outline-color .2s, border-color .2s, box-shadow .2s;
}

/* ⬇️ 버튼 hover 시 부모 .slot-card에 LoA-highlight 스타일 적용 */
.slot-card:has(.apply-btn:hover) {
  position: relative;
  outline: 2px solid #a399f2;
  border-color: #a399f2 !important;
  animation: loaPulse .9s ease-in-out infinite;
  transition: outline-color .2s, border-color .2s, box-shadow .2s;
}